@import url("https://fonts.googleapis.com/css2?family=Domine:wght@600&display=swap");

/* #region Default styles */

  body, html {
    height: 100%;
  }
  
.parallax{
  background-image: url("./assets/kjas.png");
  height: 100%;  
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;  
  }



.titleContainer{
  font-family: 'Domine', 'Helvetica';
  position: -webkit-sticky;
  position: sticky;
  --maskX: 0;
  --maskY: 50; }
  .titleContainer h1 {
    font-size: 10vw;

    margin: 0;
    line-height: 1.1em; }
.titleWrapper {
  line-height: 100vw;
  color: #343434;
  cursor: pointer; }

.cloneWrapper {
  line-height: 5000 vw; 
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vw;
  color: #A6A6A6;
  -webkit-transition: all 0.8s cubic-bezier(0.165, 0.84, 0.44, 1);
  -o-transition: all 0.8s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: all 0.8s cubic-bezier(0.165, 0.84, 0.44, 1);
  -webkit-clip-path: polygon(0 0, calc(var(--maskX) * 1% + (var(--maskY) - 50) * .4%) 0, calc(var(--maskX) * 1% + (var(--maskY) - 50) * -.4%) 100%, 0 100%);
          clip-path: polygon(0 0, calc(var(--maskX) * 1% + (var(--maskY) - 50) * .4%) 0, calc(var(--maskX) * 1% + (var(--maskY) - 50) * -.4%) 100%, 0 100%); }

  .video {
    position: absolute;
  left: 50%; /* relative to nearest positioned ancestor or body element */
  top: 50%; /*  relative to nearest positioned ancestor or body element */
  -webkit-transform: translate(-50%, -32%);
      -ms-transform: translate(-50%, -32%);
          transform: translate(-50%, -32%); /* relative to element's height & width */
    width: 100%;
    height: 150vw;
    display:block; }
    
  .pdf {
    color: rgb(36, 36, 36); font-size: 30px; 
     line-height: 30px; padding: 16px; border-radius: 7px; 
     font-family: 'Domine', 'Helvetica'; font-weight: 700; 
     text-decoration: none; font-style: normal; 
     font-variant: small-caps; text-transform: capitalize; 
     background-image: -webkit-gradient(linear, left top, right top, from(rgb(87,192,255)), color-stop(50%, rgb(87,192,255)),to(rgb(87,192,255))); 
     background-image: -webkit-linear-gradient(left, rgb(87,192,255) 0%, rgb(87,192,255)50%,rgb(87,192,255)100%); 
     background-image: -o-linear-gradient(left, rgb(87,192,255) 0%, rgb(87,192,255)50%,rgb(87,192,255)100%); 
     background-image: linear-gradient(to right, rgb(87,192,255) 0%, rgb(87,192,255)50%,rgb(87,192,255)100%); 
    position:absolute;
    left: 45%;
     border: 2px solid rgb(0, 0, 0); display: inline-block;}
    .pdf:hover {
    background: #f1f1f5; }
    .pdf:active {
    background: #bbc8d1; }

/* #endregion */



html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

figure {
  margin: 0;
  position: relative;
}
figure h1 {
  position: absolute;
  top: 20px;
  left: 20px;
  border-right: 3px solid #fff;
  padding: 5px 10px 5px 5px;
  
}
figcaption{
  color: #fff;
  position:relative;
  top: 1vw;
 
}


.stretch {
  height: 2000%;
  background:rgb(59, 59, 68) ;
  min-height: 100%;
  min-width: 100%;
  background-size: cover;
  margin-top: 60vw;

}
.stretch h1 {
  padding: 20px;
  color: rgb(255, 255, 255);
}
